import React, { Component } from 'react';
import ReactPhoneInput from 'react-phone-input';
import axios from 'axios';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';


class Form extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isDisabled: 1,
            phone: '',
            loader: 0,
            showThankyou: 0,
            className: 'thankyou',
            opacity: 1
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);

    }

    handleChange(value) {

        if (value.length < 5 || this.state.phone.length < 5) {
            this.setState({isDisabled: 1});
        } else {
            this.setState({isDisabled: 0});
        }

        this.setState({
            phone: value
        });


    }

    handleSubmit(event) {

        var _this = this;


        this.setState({loader: 1});

        this.setState({
            className: _this.state.loader ? 'loader loader--style3 active' : 'loader loader--style3'
        });


        /*
         Do whatever needs to do with ajax.
         */

        /*
            axios({
                method: 'post',
                url: './dev.php',
                data: {
                    phone: _this.state.phone
                }
            }).then(function (response) {




            }).catch(function (error) {

                console.log("error: " + error);

            });

        */

        _this.setState({
            showThankyou: 1
        });

        _this.setState({
            loader: 0,
            isDisabled: 1,
            className: _this.state.showThankyou ? 'thankyou active' : 'thankyou'
        });

        console.log('ok' + _this.state.showThankyou);

        event.preventDefault();
    }

    render() {

        var className = this.state.className;

        return (
            <section id="callme" className="callme">
                <ReactCSSTransitionGroup
                transitionName="example"
                transitionEnterTimeout={500}
                transitionLeaveTimeout={300}>
                    <Messages delay={10000} divclass={className} />
                </ReactCSSTransitionGroup>

                <form onSubmit={this.handleSubmit}>

                    <ReactPhoneInput defaultCountry={'gb'} onChange={this.handleChange} />
                    <input type="submit" value="Call me" disabled={this.state.isDisabled} />

                </form>
            </section>
        );
    }
}

class Messages extends Component {

    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            jobs: []
        };

    }

    componentDidMount() {
        var _this = this;
        this.serverRequest =
            axios
                .get("https://codepen.io/jobs.json")
                .then(function (result) {
                    _this.setState({
                        jobs: result.data.jobs
                    });
                })
    }

    componentWillUnmount() {
        this.serverRequest.abort();
    }

    render() {

        return (
            <div className={this.props.divclass} >

                    {this.state.jobs.map(function (job) {
                        return (
                            <div key={job.url} className="job">
                                <a href={job.url}>
                                    {job.company_name}
                                is looking for a
                                    {job.term}
                                    {job.title}
                                </a>
                            </div>
                        );
                    })}

            </div>
        )
    }


}


class App extends Component {
    render() {
        return (
            <Form />
        );
    }
}

export default App;



